import Api from '../../api/wo';
import mpvuePicker from 'mpvue-picker';
	export default {
		components: {
			mpvuePicker	
		},
		onLoad:function(){
			Api.initWO().then(res => {
					this.bkName = res.data.org
					this.machinePickerArray = res.data.machinePicker
					this.faultPickerArray = res.data.faultPicker
					this.multiArray[0] = res.data.machinePicker
					this.multiArray[1] = res.data.machinePicker[0].children
					console.log(this.multiArray)
				})
		},
		data() {
			return {
				index: -1,
				imgList: [],
				bkName: '',
				multiArray: [],
				multiIndex: [0, 0],
				machinePickerArray: [],
				faultPickerArray: [],
				flag: false
			};
		},
		methods: {
			ChooseImage() {
				uni.chooseImage({
					count: 4, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album','camera'], //从相册选择
					success: (res) => {
						if (this.imgList.length != 0) {
							this.imgList = this.imgList.concat(res.tempFilePaths)
						} else {
							this.imgList = res.tempFilePaths
						}
					}
				});
			},
			ViewImage(e) {
				uni.previewImage({
					urls: this.imgList,
					current: e.currentTarget.dataset.url
				});
			},
			DelImg(e) {
				uni.showModal({
					title: '删除确认',
					content: '确定要删除这张图片吗？',
					cancelText: '取消',
					confirmText: '确认',
					success: res => {
						if (res.confirm) {
							this.imgList.splice(e.currentTarget.dataset.index, 1)
						}
					}
				})
			},
			PickerChange(e) {
				console.log(e.detail.value)
				this.index = e.detail.value
			},
			MultiChange(e) {
				console.log(e.detail.value)
				this.multiIndex = e.detail.value
			},
			MultiColumnChange(e) {
				let data = {
					multiArray: this.multiArray,
					multiIndex: this.multiIndex
				};
				let len = data.multiArray.length
				console.log(e.detail.value)
				console.log(e.detail.column)
				data.multiIndex[e.detail.column] = e.detail.value
				for(let i = 0 ; i < len ; i++){
					if(i == 5){
						data.multiArray[1] = []
						break
					}
					if(data.multiIndex[0] == i){
						console.log(data.multiArray[0][i])
						data.multiArray[1] = data.multiArray[0][i].children
						break
					}
				}
				data.multiIndex[1] = 0
				this.multiArray = data.multiArray
				this.multiIndex = data.multiIndex
				console.log(this.multiArray)
			},
		}
	}
